.foldableText {
  position: relative; }
  .foldableText--fold {
    max-height: 84px;
    overflow: hidden; }
  .foldableText__label {
    position: absolute;
    padding: 0 6px 0 20px;
    bottom: 0;
    right: 0;
    line-height: 28px;
    font-size: 14px;
    color: #6a9ada;
    background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(#fff), color-stop(#fff), to(#fff));
    background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), #fff, #fff, #fff);
    background: -o-linear-gradient(left, rgba(255, 255, 255, 0), #fff, #fff, #fff);
    background: linear-gradient(90deg, rgba(255, 255, 255, 0), #fff, #fff, #fff); }



