.imageSlide {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000; }
  .imageSlide__index {
    margin-top: 10px;
    width: 100%;
    color: #fff;
    font-size: 14px;
    text-align: center;
    line-height: 28px; }
  .imageSlide__content {
    width: 100%;
    height: 100%; }
    .imageSlide__content .slick-list {
      height: 100%; }
    .imageSlide__content .slick-track {
      height: 100%; }
  .imageSlide__item {
    position: relative;
    height: 100%; }
    .imageSlide__item img {
      position: absolute;
      margin-top: 240px;
      top: 50%;
      left: 50%;
      width: 100%;
      height: auto;
      -webkit-transform: translate3d(-50%, -50%, 0);
              transform: translate3d(-50%, -50%, 0); }



