.detail {
  overflow-x: hidden; }
  .detail__top {
    padding: 0 20px 0;
    background-color: #d4b267; }
  .detail__content {
    padding: 0 20px 40px; }
  .detail__module {
    padding: 20px 0; }
    .detail__module:nth-child(n+2) {
      border-top: 1px solid #e4e2e2; }
    .detail__module--title {
      font-size: 18px;
      line-height: 32px;
      margin-bottom: 10px;
      font-weight: bold; }
  .detail__overview {
    color: #333;
    font-size: 14px;
    line-height: 28px; }
  .detail__buyBtn {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 55px;
    font-size: 16px;
    line-height: 55px;
    color: #fff;
    letter-spacing: 2px;
    text-align: center;
    background-color: #ef8191; }



