.home__silde {
  position: relative;
  width: 100%;
  padding-bottom: 40%; }
  .home__silde__slideWrap {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0; }

.home__content {
  padding: 0 20px; }
  .home__content li {
    list-style: none; }
    .home__content li:nth-child(n + 2) {
      border-top: 1px solid #e4e2e2; }

.home__menu {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 48px;
  background-color: #fff;
  border-top: 1px solid #e4e2e2; }



