.seat__top {
  padding: 0 20px;
  background-color: #660033; }

.seat__tip {
  height: 24px;
  padding: 10px 0;
  background: #fff url("/source/seats.svg") 50% 50%/auto 24px no-repeat; }

.seat__graph {
  width: 100%;
  height: 500px;
  background-color: #f7f7f7;
  overflow-x: scroll; }

.seat__screen {
  height: 28px;
  font-size: 14px;
  text-align: center;
  line-height: 28px;
  background: transparent url("/source/screen.svg") 50% 0/auto 28px no-repeat; }

.seat__wrap {
  width: 100%;
  overflow-x: scroll; }



