.posterSlide {
  width: 100%;
  height: 100%;
  overflow: hidden; }
  .posterSlide__image {
    width: 100%;
    height: 100%; }
  .posterSlide__dots {
    position: absolute;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 20px;
    bottom: 10px;
    text-align: center; }
    .posterSlide__dots li {
      list-style: none;
      display: inline-block;
      width: 8px;
      height: 8px; }
      .posterSlide__dots li:nth-child(n+2) {
        margin-left: 15px; }
      .posterSlide__dots li::before {
        content: '';
        display: block;
        width: 8px;
        height: 8px;
        background: rgba(255, 255, 255, 0.6); }
      .posterSlide__dots li.slick-active::before {
        background-color: #fff; }
      .posterSlide__dots li button {
        position: absolute;
        bottom: 4px;
        font-size: 0;
        line-height: 0;
        background-color: transparent;
        height: 10px;
        width: 6px;
        margin-left: -8px;
        border: none; }



